body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}
.white {
  color: white;
}

.footer_icon {
  color: white;
  padding: 10px 10px;
  font-size: 24px;
}
.footer_icon:hover {
  color: #fdba66;
}

.footer_image {
  z-index: 2;
  box-shadow: 20px -15px #05595b;
}
.footer_content {
  max-width: 415px;
  padding-left: 100px;
}

.footer_content > p {
  font-size: 24px;
  font-weight: 400;
  padding-bottom: 15px;
}
.footer_content > h2 {
  font-size: 36px;
  font-weight: 500;
  padding-bottom: 31px;
}
.footer_content > h3 {
  font-size: 24px;
  font-weight: 500;
  padding-bottom: 35px;
}
.footer_bottom_left > p {
  font-size: 16px;
  color: white;
  font-weight: 500;
  padding: 10px 0;
}
.footer_bottom_left > h3 {
  font-size: 24px;
  color: white;
  font-weight: 500;
}
.button_text {
  padding: 10px;
  font-size: 16px;
}

